import { useState, useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Card } from "@/components/ui/card";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Navigation, Maximize, Minimize, MapPin, Star, MoreHorizontal, Filter, ChevronUp, ChevronDown, AlertTriangle } from "lucide-react";
import { useRestaurants, useRestaurantTypes } from "@/hooks/useRestaurants";
import { useTranslation } from "react-i18next";
import { calculateDistance, getUserLocation } from "@/lib/geolocation";
import RestaurantMap from "@/components/RestaurantMap";
import { useToast } from "@/hooks/use-toast";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import mapboxgl from "mapbox-gl";
import { useActivities } from "@/hooks/useActivities";
import { supabase } from "@/integrations/supabase/client";
import { useActivityRestaurantCounts } from "@/hooks/useActivityRestaurantCounts";
import { isIOSWebView } from "@/lib/externalLinks";
import mapIcon from "@/assets/map-icon.png";
import logoIcon from "@/assets/xiaoxiong-logo-clean.png";
import LocationPermissionDialog from "@/components/LocationPermissionDialog";


const MapRestaurantTags = ({ restaurant, language }: { 
  restaurant: any; 
  language: string;
}) => {
  const [visibleCount, setVisibleCount] = useState(999);
  const containerRef = useRef<HTMLDivElement>(null);
  
  const allTags = restaurant.type.slice(0, 3).map((type: string, idx: number) => ({ 
    key: `type-${idx}`, 
    label: type, 
    variant: "secondary" as const,
    color: undefined 
  }));
  
  useEffect(() => {
    const checkOverflow = () => {
      if (!containerRef.current) return;
      
      const container = containerRef.current;
      const badges = Array.from(container.querySelectorAll('.tag-badge')) as HTMLElement[];
      
      if (badges.length === 0) {
        setTimeout(checkOverflow, 50);
        return;
      }
      
      const firstBadgeTop = badges[0]?.offsetTop;
      let lastVisibleIndex = badges.length;
      
      // Find where tags overflow to second line
      for (let i = 0; i < badges.length; i++) {
        if (badges[i].offsetTop > firstBadgeTop) {
          lastVisibleIndex = i;
          break;
        }
      }
      
      // If there's overflow, reduce by 1 more to make room for "..." button
      if (lastVisibleIndex < badges.length) {
        lastVisibleIndex = Math.max(1, lastVisibleIndex - 1);
      }
      
      setVisibleCount(lastVisibleIndex);
    };
    
    setVisibleCount(999);
    const timer = setTimeout(checkOverflow, 100);
    
    window.addEventListener('resize', checkOverflow);
    return () => {
      clearTimeout(timer);
      window.removeEventListener('resize', checkOverflow);
    };
  }, [allTags.length, restaurant.id]);
  
  const visibleTags = allTags.slice(0, visibleCount);
  const hasMore = allTags.length > visibleCount;
  
  return (
    <div className="flex items-center gap-2">
      <div ref={containerRef} className="flex gap-1.5 flex-wrap flex-1">
        {visibleTags.map((tag) => (
          <Badge 
            key={tag.key} 
            variant={tag.variant}
            className="text-xs font-medium whitespace-nowrap tag-badge"
            style={tag.color ? { backgroundColor: tag.color, color: 'white' } : undefined}
          >
            {tag.label}
          </Badge>
        ))}
      </div>
      {hasMore && (
        <Popover>
          <PopoverTrigger asChild>
            <Button variant="ghost" size="sm" className="h-6 px-2 text-muted-foreground hover:text-foreground flex-shrink-0">
              <MoreHorizontal className="h-4 w-4" />
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-80">
            <div className="flex flex-wrap gap-2">
              {allTags.map((tag) => (
                <Badge 
                  key={tag.key} 
                  variant={tag.variant}
                  className="text-xs font-medium"
                  style={tag.color ? { backgroundColor: tag.color, color: 'white' } : undefined}
                >
                  {tag.label}
                </Badge>
              ))}
            </div>
          </PopoverContent>
        </Popover>
      )}
    </div>
  );
};

const Map = () => {
  const navigate = useNavigate();
  const { t, i18n } = useTranslation();
  const { toast } = useToast();
  const { localizeRestaurant } = useLocalizedContent();
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedTypes, setSelectedTypes] = useState<string[]>([]);
  const [selectedActivity, setSelectedActivity] = useState<string | null>(null);
  const [activityRestaurantIds, setActivityRestaurantIds] = useState<string[]>([]);
  const [radiusFilter, setRadiusFilter] = useState<number | null>(null);
  const [userLocation, setUserLocation] = useState<{ lat: number; lng: number } | null>(null);
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [showAllTypes, setShowAllTypes] = useState(false);
  const [showAllActivities, setShowAllActivities] = useState(false);
  const mapRef = useRef<mapboxgl.Map | null>(null);
  const savedMapState = useRef<{ center: [number, number]; zoom: number } | null>(null);
  const restaurantRefs = useRef<{ [key: string]: HTMLDivElement | null }>({});
  const [selectedRestaurantId, setSelectedRestaurantId] = useState<string | null>(null);
  const [showFilters, setShowFilters] = useState(false);
  const [showLocationDialog, setShowLocationDialog] = useState(false);
  const [isMapCollapsed, setIsMapCollapsed] = useState(false);

  const { data: restaurants, isLoading } = useRestaurants(searchQuery, selectedTypes.length > 0 ? selectedTypes.join(',') : undefined);
  const { data: restaurantTypes = [] } = useRestaurantTypes();
  const { data: activities = [] } = useActivities();
  const { data: activityRestaurantCounts = {} } = useActivityRestaurantCounts();

  // Filter activities to only show those with at least 1 restaurant
  const activitiesWithRestaurants = activities.filter(activity => (activityRestaurantCounts[activity.id] || 0) > 0);

  const handleTypeFilter = (type: string) => {
    setSelectedTypes(prev => {
      if (prev.includes(type)) {
        // Remove type if already selected
        return prev.filter(t => t !== type);
      } else {
        // Add type to selection
        return [...prev, type];
      }
    });
  };

  const handleRequestLocation = () => {
    setShowLocationDialog(true);
  };

  const requestLocation = async () => {
    setShowLocationDialog(false);
    try {
      const position = await getUserLocation();
      setUserLocation({
        lat: position.coords.latitude,
        lng: position.coords.longitude,
      });
      toast({
        title: t("restaurants.locationEnabled"),
        description: t("restaurants.sortByDistance"),
      });
    } catch (error: any) {
      console.error("Location error:", error);
      // Silently handle location errors - no toast notification
    }
  };
  const handleRadiusChange = (radius: number | null) => {
    setRadiusFilter(radius);
    if (radius && !userLocation) {
      handleRequestLocation();
    }
  };

  const handleActivityFilter = async (activityId: string) => {
    if (selectedActivity === activityId) {
      setSelectedActivity(null);
      setActivityRestaurantIds([]);
    } else {
      setSelectedActivity(activityId);
      // Fetch restaurants for this activity
      const { data, error } = await supabase
        .from("activity_restaurants")
        .select("restaurant_id")
        .eq("activity_id", activityId);
      
      if (error) {
        console.error("Error fetching activity restaurants:", error);
        setActivityRestaurantIds([]);
      } else {
        setActivityRestaurantIds(data.map(ar => ar.restaurant_id));
      }
    }
  };

  // Localize and process restaurants
  const processedRestaurants = restaurants?.map(restaurant => {
    const localized = localizeRestaurant(restaurant);
    if (userLocation) {
      const distance = calculateDistance(
        userLocation.lat,
        userLocation.lng,
        restaurant.lat,
        restaurant.lng
      );
      return { ...localized, distance };
    }
    return localized;
  }) || [];

  // Filter by activity - when activity is selected but has no restaurants, show empty list
  const activityFilteredRestaurants = selectedActivity 
    ? processedRestaurants.filter(r => activityRestaurantIds.includes(r.id)) 
    : processedRestaurants;

  // Filter by radius and sort: by distance if location available, otherwise by rating (high to low)
  const filteredRestaurants = radiusFilter && userLocation
    ? activityFilteredRestaurants
        .filter(r => r.distance && r.distance <= radiusFilter)
        .sort((a, b) => (a.distance || 0) - (b.distance || 0))
    : userLocation 
      ? activityFilteredRestaurants.sort((a, b) => (a.distance || 0) - (b.distance || 0))
      : activityFilteredRestaurants.sort((a, b) => (b.rating || 0) - (a.rating || 0));

  const handleRestaurantClick = (restaurant: any) => {
    navigate(`/restaurant/${restaurant.id}`);
  };

  const handleMarkerClick = (restaurant: any) => {
    setSelectedRestaurantId(restaurant.id);
    
    const restaurantElement = restaurantRefs.current[restaurant.id];
    if (restaurantElement) {
      restaurantElement.scrollIntoView({ 
        behavior: 'smooth', 
        block: 'center'
      });
    }
  };

  const toggleFullscreen = () => {
    const mapContainer = document.getElementById('map-container');
    if (!mapContainer) return;

    if (!isFullscreen) {
      // Save current map state before entering fullscreen
      if (mapRef.current) {
        const center = mapRef.current.getCenter();
        const zoom = mapRef.current.getZoom();
        savedMapState.current = { center: [center.lng, center.lat], zoom };
      }
      // Enter fullscreen
      if (mapContainer.requestFullscreen) {
        mapContainer.requestFullscreen();
      }
    } else {
      // Exit fullscreen
      if (document.exitFullscreen) {
        document.exitFullscreen();
      }
    }
  };

  // Listen for fullscreen changes (ESC key, browser controls, etc.)
  useEffect(() => {
    const handleFullscreenChange = () => {
      const isNowFullscreen = !!document.fullscreenElement;
      setIsFullscreen(isNowFullscreen);
      
      // Restore map position when exiting fullscreen
      if (!isNowFullscreen && savedMapState.current && mapRef.current) {
        setTimeout(() => {
          mapRef.current?.jumpTo({
            center: savedMapState.current!.center,
            zoom: savedMapState.current!.zoom,
          });
        }, 100);
      }
    };

    document.addEventListener('fullscreenchange', handleFullscreenChange);
    return () => {
      document.removeEventListener('fullscreenchange', handleFullscreenChange);
    };
  }, []);

  return (
    <div className="min-h-screen bg-secondary/20 py-8 px-4 animate-fade-in">
      <div className="container mx-auto max-w-7xl lg:h-[100dvh] lg:flex lg:flex-col">
        {/* Header */}
        <div className="mb-6 flex-shrink-0">
          <h1 className="text-4xl font-bold mb-2 bg-gradient-to-r from-primary to-primary/70 bg-clip-text text-transparent">
            {t("nav.mapView")}
          </h1>
          <p className="text-muted-foreground">{t("map.subtitle")}</p>
        </div>
          
        {/* Search and Filters */}
        <div className="mb-6 space-y-4 flex-shrink-0">
          <div className="flex flex-col md:flex-row gap-3">
            <div className="flex-1 max-w-md">
              <Input
                placeholder={t("restaurants.search")}
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="h-11 bg-background/60 backdrop-blur-sm border-primary/20 focus:border-primary"
              />
            </div>
            
            {/* Location and Filter Buttons - Side by side on mobile */}
            <div className="flex gap-2 md:contents">
              <Button
                variant={userLocation ? "default" : "outline"}
                onClick={userLocation ? undefined : handleRequestLocation}
                className="flex-1 md:flex-initial gap-2 h-11 md:px-6"
              >
                <Navigation className={`w-4 h-4 ${userLocation ? 'animate-pulse' : ''}`} />
                <span className="md:hidden">{t("restaurants.getLocationMobile")}</span>
                <span className="hidden md:inline">{userLocation ? t("restaurants.locationEnabled") : t("restaurants.getLocation")}</span>
              </Button>

              {/* Filter Toggle Button */}
              <Button
                variant={showFilters ? "default" : "outline"}
                onClick={() => setShowFilters(!showFilters)}
                className="flex-1 md:flex-initial gap-2 h-11 md:px-6"
              >
                <Filter className="w-4 h-4" />
                {t("restaurants.filter")}
              </Button>
            </div>
          </div>

          {/* Filters Container */}
          {showFilters && (
            <div className="space-y-3 bg-card/50 backdrop-blur-sm rounded-lg p-4 border border-border/50 animate-fade-in">
            {/* Type Filters Section */}
            <div className="space-y-2">
              <div className="flex items-center gap-2 flex-wrap">
                <span className="text-sm font-semibold text-foreground">{t("restaurants.restaurantTypes")}:</span>
                {selectedTypes.length > 0 && (
                  <Badge
                    variant="outline"
                    className="cursor-pointer hover:bg-destructive hover:text-destructive-foreground hover:scale-105 transition-all duration-200 px-2 py-0 text-xs h-5"
                    onClick={() => setSelectedTypes([])}>
                    {t("common.actions.clearFilter")}
                  </Badge>
                )}
                {restaurantTypes.length > 8 && (
                  <Button
                    variant="ghost"
                    size="sm"
                    className="h-5 px-2 text-xs text-primary hover:text-primary/80 ml-auto hidden sm:flex transition-all duration-200"
                    onClick={() => setShowAllTypes(!showAllTypes)}
                  >
                    {showAllTypes ? t("common.showLess") : t("common.showMore")}
                  </Button>
                )}
              </div>
              {/* Desktop: flex-wrap, Mobile: horizontal scroll with gradient indicators */}
              <div className="relative overflow-hidden">
                <div className={`hidden sm:flex gap-2 flex-wrap transition-all duration-300 ease-out ${showAllTypes ? 'animate-expand-height' : ''}`}>
                  {(showAllTypes ? restaurantTypes : restaurantTypes.slice(0, 8)).map((type, index) => (
                    <Badge
                      key={type}
                      variant={selectedTypes.includes(type) ? "default" : "secondary"}
                      className={`cursor-pointer transition-all duration-200 px-3 py-1.5 text-xs whitespace-nowrap hover:scale-105 active:animate-tag-select ${selectedTypes.includes(type) ? 'ring-2 ring-primary/30 shadow-sm' : ''}`}
                      style={{ animationDelay: `${index * 30}ms` }}
                      onClick={() => handleTypeFilter(type)}
                    >
                      {type}
                    </Badge>
                  ))}
                </div>
                {/* Mobile: scrollable with gradient indicators */}
                <div className="sm:hidden relative -mx-1 px-1">
                  <div className="flex gap-2 overflow-x-auto pb-2 scrollbar-hide snap-x snap-mandatory scroll-smooth">
                    {restaurantTypes.map((type, index) => (
                      <Badge
                        key={type}
                        variant={selectedTypes.includes(type) ? "default" : "secondary"}
                        className={`cursor-pointer transition-all duration-200 px-3 py-1.5 text-xs whitespace-nowrap flex-shrink-0 snap-start hover:scale-105 active:animate-tag-select ${selectedTypes.includes(type) ? 'ring-2 ring-primary/30 shadow-sm' : ''}`}
                        style={{ animationDelay: `${index * 30}ms` }}
                        onClick={() => handleTypeFilter(type)}
                      >
                        {type}
                      </Badge>
                    ))}
                  </div>
                  {/* Gradient indicators for scrollable content */}
                  <div className="absolute left-0 top-0 bottom-2 w-4 bg-gradient-to-r from-card via-card/60 to-transparent pointer-events-none" />
                  <div className="absolute right-0 top-0 bottom-2 w-4 bg-gradient-to-l from-card via-card/60 to-transparent pointer-events-none" />
                </div>
              </div>
            </div>

            {/* Activity Filters Section */}
            {activitiesWithRestaurants.length > 0 && (
              <div className="space-y-2 pt-1 border-t border-border/30">
                <div className="flex items-center gap-2 flex-wrap">
                  <span className="text-sm font-semibold text-foreground">{t("nav.activities")}:</span>
                  {selectedActivity && (
                    <Badge
                      variant="outline"
                      className="cursor-pointer hover:bg-destructive hover:text-destructive-foreground hover:scale-105 transition-all duration-200 px-2 py-0 text-xs h-5"
                      onClick={() => {
                        setSelectedActivity(null);
                        setActivityRestaurantIds([]);
                      }}>
                      {t("common.actions.clearFilter")}
                    </Badge>
                  )}
                  {activitiesWithRestaurants.length > 8 && (
                    <Button
                      variant="ghost"
                      size="sm"
                      className="h-5 px-2 text-xs text-primary hover:text-primary/80 ml-auto hidden sm:flex transition-all duration-200"
                      onClick={() => setShowAllActivities(!showAllActivities)}
                    >
                      {showAllActivities ? t("common.showLess") : t("common.showMore")}
                    </Button>
                  )}
                </div>
                <div className="relative overflow-hidden">
                  <div className={`hidden sm:flex gap-2 flex-wrap transition-all duration-300 ease-out ${showAllActivities ? 'animate-expand-height' : ''}`}>
                    {(showAllActivities ? activitiesWithRestaurants : activitiesWithRestaurants.slice(0, 8)).map((activity, index) => {
                      const activityLabel = i18n.language === 'zh' 
                        ? (activity.badge_text_zh || activity.title_zh)
                        : i18n.language === 'pt' 
                        ? (activity.badge_text_pt || activity.title_pt)
                        : (activity.badge_text_en || activity.title_en);
                      return (
                        <Badge
                          key={activity.id}
                          variant={selectedActivity === activity.id ? "default" : "secondary"}
                          className={`cursor-pointer transition-all duration-200 px-3 py-1.5 text-xs whitespace-nowrap hover:scale-105 active:animate-tag-select ${selectedActivity === activity.id ? 'ring-2 ring-primary/30 shadow-sm' : ''}`}
                          style={{ animationDelay: `${index * 30}ms` }}
                          onClick={() => handleActivityFilter(activity.id)}
                        >
                          {activityLabel}
                        </Badge>
                      );
                    })}
                  </div>
                  {/* Mobile: scrollable with gradient indicators */}
                  <div className="sm:hidden relative -mx-1 px-1">
                    <div className="flex gap-2 overflow-x-auto pb-2 scrollbar-hide snap-x snap-mandatory scroll-smooth">
                      {activitiesWithRestaurants.map((activity, index) => {
                        const activityLabel = i18n.language === 'zh' 
                          ? (activity.badge_text_zh || activity.title_zh)
                          : i18n.language === 'pt' 
                          ? (activity.badge_text_pt || activity.title_pt)
                          : (activity.badge_text_en || activity.title_en);
                        return (
                          <Badge
                            key={activity.id}
                            variant={selectedActivity === activity.id ? "default" : "secondary"}
                            className={`cursor-pointer transition-all duration-200 px-3 py-1.5 text-xs whitespace-nowrap flex-shrink-0 snap-start hover:scale-105 active:animate-tag-select ${selectedActivity === activity.id ? 'ring-2 ring-primary/30 shadow-sm' : ''}`}
                            style={{ animationDelay: `${index * 30}ms` }}
                            onClick={() => handleActivityFilter(activity.id)}
                          >
                            {activityLabel}
                          </Badge>
                        );
                      })}
                    </div>
                    {/* Gradient indicators for scrollable content */}
                    <div className="absolute left-0 top-0 bottom-2 w-4 bg-gradient-to-r from-card via-card/60 to-transparent pointer-events-none" />
                    <div className="absolute right-0 top-0 bottom-2 w-4 bg-gradient-to-l from-card via-card/60 to-transparent pointer-events-none" />
                  </div>
                </div>
              </div>
            )}
            </div>
          )}

          {/* Radius Filter */}
          {userLocation && (
            <div className="flex gap-2 flex-wrap items-center bg-muted/30 backdrop-blur-sm rounded-lg p-3 border border-primary/10">
              <span className="text-sm font-semibold text-primary">{t("restaurants.radius")}:</span>
              <Badge
                variant={radiusFilter === null ? "default" : "secondary"}
                className="cursor-pointer hover:scale-105 transition-transform"
                onClick={() => handleRadiusChange(null)}
              >
                {t("restaurants.allRestaurants")}
              </Badge>
              <Badge
                variant={radiusFilter === 5 ? "default" : "secondary"}
                className="cursor-pointer hover:scale-105 transition-transform"
                onClick={() => handleRadiusChange(5)}
              >
                {t("restaurants.radius5km")}
              </Badge>
              <Badge
                variant={radiusFilter === 10 ? "default" : "secondary"}
                className="cursor-pointer hover:scale-105 transition-transform"
                onClick={() => handleRadiusChange(10)}
              >
                {t("restaurants.radius10km")}
              </Badge>
              <Badge
                variant={radiusFilter === 20 ? "default" : "secondary"}
                className="cursor-pointer hover:scale-105 transition-transform"
                onClick={() => handleRadiusChange(20)}
              >
                {t("restaurants.radius20km")}
              </Badge>
            </div>
          )}
        </div>

        {/* Map and List Layout */}
        {!isLoading && filteredRestaurants && (
          <div className="flex flex-col lg:flex-row gap-6 lg:flex-1 lg:min-h-0 lg:overflow-hidden">
            {/* Map - Fixed on desktop, collapsible on mobile */}
            <div className={`w-full lg:flex-1 transition-all duration-300 ease-in-out ${isMapCollapsed ? 'h-0 overflow-hidden lg:h-full' : 'h-[400px] lg:h-full'}`}>
              <div id="map-container" className={`${isFullscreen ? 'fixed inset-0 z-50 bg-background' : 'w-full h-full'} rounded-xl overflow-hidden shadow-2xl border border-primary/10 relative group`}>
                {/* Fullscreen Toggle Button */}
                <Button
                  variant="secondary"
                  size="icon"
                  className="absolute bottom-4 right-4 z-10 shadow-lg opacity-0 group-hover:opacity-100 transition-opacity"
                  onClick={toggleFullscreen}
                >
                  {isFullscreen ? (
                    <Minimize className="w-5 h-5" />
                  ) : (
                    <Maximize className="w-5 h-5" />
                  )}
                </Button>
                <div className="h-full relative">
                  <RestaurantMap
                    restaurants={filteredRestaurants}
                    onRestaurantClick={handleRestaurantClick}
                    userLocation={userLocation}
                    radiusFilter={radiusFilter}
                    onMarkerClick={handleMarkerClick}
                    showViewDetailsButton={true}
                    onMapInit={(mapInstance) => {
                      mapRef.current = mapInstance;
                    }}
                  />
                  {/* Logo and text in bottom left corner of map */}
                  <div className="absolute bottom-4 left-4 z-10 pointer-events-none flex items-center gap-2 bg-background/80 backdrop-blur-sm px-3 py-2 rounded-full shadow-lg">
                    <img 
                      src={logoIcon} 
                      alt="Xiaoxiong Market Logo" 
                      className="h-6 w-6 object-contain"
                    />
                    <span className="text-sm font-semibold text-foreground whitespace-nowrap">
                      {i18n.language === 'zh' ? '小熊市场' : 'Xiaoxiong Market'}
                    </span>
                  </div>
                </div>
              </div>
            </div>
            
            {/* Mobile Map Toggle Button */}
            <Button
              variant="outline"
              size="sm"
              className="lg:hidden flex items-center justify-center gap-2 -mt-3 mb-1"
              onClick={() => setIsMapCollapsed(!isMapCollapsed)}
            >
              {isMapCollapsed ? (
                <>
                  <ChevronDown className="w-4 h-4" />
                  {t("map.showMap")}
                </>
              ) : (
                <>
                  <ChevronUp className="w-4 h-4" />
                  {t("map.hideMap")}
                </>
              )}
            </Button>

            {/* Restaurant List Sidebar - Scrollable independently */}
            <div className="w-full lg:w-[420px] flex flex-col lg:min-h-0 lg:h-full">
              <div className="mb-4 pb-4 border-b border-border flex-shrink-0">
                <h2 className="text-2xl font-bold text-foreground flex items-center gap-2">
                  <MapPin className="w-6 h-6 text-primary" />
                  {t("restaurants.title")}
                  <Badge variant="secondary" className="ml-2">{filteredRestaurants.length}</Badge>
                </h2>
                {userLocation && (
                  <p className="text-sm text-muted-foreground mt-2 flex items-center gap-1.5">
                    <Navigation className="w-4 h-4" />
                    {t("restaurants.sortedByDistance")}
                  </p>
                )}
              </div>
              
              {/* Restaurant list - independent scrollable area */}
              {filteredRestaurants.length === 0 ? (
                <div className="flex flex-col items-center justify-center py-12 text-center">
                  <MapPin className="w-16 h-16 text-muted-foreground/40 mb-4" />
                  <h3 className="text-xl font-semibold text-foreground mb-2">
                    {t("restaurants.noResults")}
                  </h3>
                  <p className="text-muted-foreground max-w-xs">
                    {t("restaurants.noResultsDesc")}
                  </p>
                </div>
              ) : (
                <>
                  <div className="lg:hidden space-y-3 pb-4">
                    {filteredRestaurants.map((restaurant) => (
                  <Card
                    key={restaurant.id}
                    ref={(el) => restaurantRefs.current[restaurant.id] = el}
                    className={`p-5 cursor-pointer hover:shadow-xl transition-all duration-300 hover:scale-[1.02] hover:border-primary/40 border-2 bg-gradient-to-br from-card to-card/50 backdrop-blur-sm ${
                      selectedRestaurantId === restaurant.id 
                        ? 'border-primary ring-2 ring-primary ring-offset-2' 
                        : 'border-transparent'
                    }`}
                    onClick={() => handleRestaurantClick(restaurant)}
                  >
                    <div className="flex flex-col gap-3">
                      <div className="flex items-start justify-between gap-2">
                        <div className="flex flex-col gap-2">
                          <h3 className="font-bold text-lg text-foreground line-clamp-1 flex-1">
                            {restaurant.name}
                          </h3>
                          {restaurant.distance !== undefined && (
                            <div className="flex items-center gap-1.5 bg-primary/10 px-2.5 py-1 rounded-full w-fit">
                              <Navigation className="w-4 h-4 text-primary" />
                              <span className="font-bold text-sm text-primary">
                                {restaurant.distance < 1 
                                  ? `${(restaurant.distance * 1000).toFixed(0)} m` 
                                  : `${restaurant.distance.toFixed(1)} km`}
                              </span>
                            </div>
                          )}
                        </div>
                        {restaurant.rating > 0 && (
                          <div className="flex items-center gap-1.5 bg-primary/10 px-2.5 py-1 rounded-full flex-shrink-0">
                            <Star className="w-4 h-4" style={{ fill: '#016450', color: '#016450' }} />
                            <span className="font-bold text-sm text-primary">{restaurant.rating}</span>
                          </div>
                        )}
                      </div>
                      
                      {restaurant.type && restaurant.type.length > 0 && (
                        <MapRestaurantTags 
                          restaurant={restaurant} 
                          language={i18n.language}
                        />
                      )}
                      
                      <div className="flex items-start gap-2 text-sm text-muted-foreground">
                        <MapPin className="w-4 h-4 mt-0.5 flex-shrink-0 text-primary/60" />
                        <span className="line-clamp-2">{restaurant.address}</span>
                      </div>
                    </div>
                    </Card>
                  ))}
                </div>
                
                <ScrollArea className="hidden lg:block lg:flex-1 pr-4">
                  <div className="space-y-3 pb-4">
                    {filteredRestaurants.map((restaurant) => (
                      <Card
                        key={restaurant.id}
                        ref={(el) => restaurantRefs.current[restaurant.id] = el}
                        className={`p-5 cursor-pointer hover:shadow-xl transition-all duration-300 hover:scale-[1.02] hover:border-primary/40 border-2 bg-gradient-to-br from-card to-card/50 backdrop-blur-sm ${
                          selectedRestaurantId === restaurant.id 
                            ? 'border-primary ring-2 ring-primary ring-offset-2' 
                            : 'border-transparent'
                        }`}
                        onClick={() => handleRestaurantClick(restaurant)}
                      >
                        <div className="flex flex-col gap-3">
                          <div className="flex items-start justify-between gap-2">
                            <div className="flex flex-col gap-2">
                              <h3 className="font-bold text-lg text-foreground line-clamp-1 flex-1">
                                {restaurant.name}
                              </h3>
                              {restaurant.distance !== undefined && (
                                <div className="flex items-center gap-1.5 bg-primary/10 px-2.5 py-1 rounded-full w-fit">
                                  <Navigation className="w-4 h-4 text-primary" />
                                  <span className="font-bold text-sm text-primary">
                                    {restaurant.distance < 1 
                                      ? `${(restaurant.distance * 1000).toFixed(0)} m` 
                                      : `${restaurant.distance.toFixed(1)} km`}
                                  </span>
                                </div>
                              )}
                            </div>
                            {restaurant.rating > 0 && (
                              <div className="flex items-center gap-1.5 bg-primary/10 px-2.5 py-1 rounded-full flex-shrink-0">
                                <Star className="w-4 h-4" style={{ fill: '#016450', color: '#016450' }} />
                                <span className="font-bold text-sm text-primary">{restaurant.rating}</span>
                              </div>
                            )}
                          </div>
                        
                        {restaurant.type && restaurant.type.length > 0 && (
                          <MapRestaurantTags 
                            restaurant={restaurant} 
                            language={i18n.language}
                          />
                        )}
                          
                          <div className="flex items-start gap-2 text-sm text-muted-foreground">
                            <MapPin className="w-4 h-4 mt-0.5 flex-shrink-0 text-primary/60" />
                            <span className="line-clamp-2">{restaurant.address}</span>
                          </div>
                        </div>
                      </Card>
                    ))}
                  </div>
                </ScrollArea>
              </>
              )}
            </div>
          </div>
        )}

      </div>

      <LocationPermissionDialog
        open={showLocationDialog}
        onOpenChange={setShowLocationDialog}
        onConfirm={requestLocation}
      />
    </div>
  );
};

export default Map;
